<template>
  <el-row :gutter="12">
    <el-col :span="24">
      <el-button class="grid-content" type="warning" @click="fractal_signMessage()">signMessage(Fractal)</el-button>
    </el-col>
    <div class="line"></div>
    <el-col :span="24">
      <div style="font-size: 12px; color: gray;">(发送到Pattern测试钱包的legacy地址)</div>
      <el-input class="grid-content" v-model="sendAmount" clearable placeholder="请输入要发送的数量" />
      <el-button class="grid-content" type="warning" @click="fractal_send()">send(Fractal)</el-button>
      <br>
      <el-input class="grid-content" v-model="sendSatoshisAmount" clearable placeholder="请输入要发送的satoshi数量" />
      <el-button class="grid-content" type="warning" @click="fractal_sendBitcoin()">sendBitcoin(Fractal)</el-button>

    </el-col>
    <div class="line"></div>
    <el-col :span="24">
      <el-input class="grid-content" v-model="psbtHex" clearable placeholder="请输入要签名的Psbt" />
      <el-button class="grid-content" type="warning" @click="fractal_signPsbt_up()">signPsbt(上架NFT)</el-button>
      <br>
      <el-button class="grid-content" type="warning"
        @click="fractal_signAndPushPsbt_up()">signAndPushPsbt(上架NFT)</el-button>
      <br>
      <div style="font-size: 12px;margin-top: 12px; margin-left: 4px; color: gray; text-align: left;">
        <li>
          1. 在OKX App选择要上架的Fractal NFT，点击【上架】
        </li>
        <li>
          2. 弹出签名面板，点开高级选项，查看
        </li>
        <li>
          3. 单击复制，就可以得到psbt。然后退出上架流程。
        </li>
        <li>
          4. 验证 https://chainquery.com/bitcoin-cli/decodepsbt
        </li>
      </div>
    </el-col>
    <div class="line"></div>
    <el-col :span="24">
      <el-input class="grid-content" v-model="nftId" clearable placeholder="请输入要转移的NFT ID" />
      <el-input class="grid-content" v-model="receiver" clearable placeholder="接收者钱包地址" />
      <el-button class="grid-content" type="warning"
        @click="fractal_sendInscription()">sendInscription(Fractal)</el-button>
      <div style="font-size: 12px;margin-top: 12px; margin-left: 4px; color: gray; text-align: left;">
        <li>
          1. 在web端NFT界面，选择要发送的Fractal NFT，打开详情
        </li>
        <li>
          2. 从NFT详情页URL中获取NFT id（url最后一部分）
        </li>
        <li>
          3. 复制到上面输入框中，再点击sendInscription按钮
        </li>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import { ref } from 'vue';

export default {
  name: 'BtcTransactionPanel',
  props: {
    msg: String
  },
  setup() {
    const nftId = ref('')
    const sendAmount = ref(0.000015)
    const sendSatoshisAmount = ref(17100)
    const psbtHex = ref('')
    const receiver = ref('')
    return {
      nftId,
      receiver,
      psbtHex,
      sendAmount,
      sendSatoshisAmount
    }
  },
  methods: {
    fractal_signMessage() {
      let params = {
        from: "bc1p7pgnqe87red4cvd7ml6rh9pl9ufpr522k2y3dpeyrvfc6g2g3r3s3ae9dr",
        signStr: "Welcome to UniSat!\n\nClick to sign in and accept the UniSat Terms of Service (https://unisat.io/terms-of-service.html) and Privacy Policy (https://unisat.io/privacy-policy.html).\n    \nThis request will not trigger a blockchain transaction.\n    \nYour authentication status will reset after 24 hours.\n    \nWallet address:\nbc1p7pgnqe87red4cvd7ml6rh9pl9ufpr522k2y3dpeyrvfc6g2g3r3s3ae9dr\n    \nNonce:\n162d70e3-8c21-4cfd-9a22-9bf0a4b7be51\n"
      };
      this.$emit('generateTransactionParams', params, "fractal_signMessage")
    },

    fractal_send() {
      if (this.sendAmount == 0) {
        alert("不能为零")
        return
      }
      let params = {
        from: '',
        to: '1NKnZ3uAuQLnmExA3WY44u1efwCgTiAxBn', // pattern测试钱包的legacy地址
        value: `${this.sendAmount}`
      }
      this.$emit('generateTransactionParams', params, "fractal_send")
    },

    fractal_sendBitcoin() {
      if (this.sendAmount == 0) {
        alert("不能为零")
        return
      }

      let params = {
        from: '',
        toAddress: '1NKnZ3uAuQLnmExA3WY44u1efwCgTiAxBn', // pattern测试钱包的legacy地址
        satoshis: `${this.sendSatoshisAmount}`,
        options: {
          feeRate: 16
        }
      }
      this.$emit('generateTransactionParams', params, "fractal_sendBitcoin")
    },

    fractal_signPsbt_up() {
      if (!this.psbtHex) {
        alert("请输入psbt")
        return
      }
      this.$emit('generateTransactionParams', {
        psbtHex: this.psbtHex,
        options: {
          autoFinalized: false
        }
      }, "fractal_signPsbt")
    },

    fractal_signAndPushPsbt_up() {
      if (!this.psbtHex) {
        alert("请输入psbt")
        return
      }
      this.$emit('generateTransactionParams', {
        psbtHex: this.psbtHex,
        options: {
          autoFinalized: true
        }
      }, "fractal_signAndPushPsbt")
    },

    fractal_sendInscription() {
      if (!this.nftId) {
        alert("请输入NFT ID")
        return
      }

      if (!this.receiver) {
        alert("请输入接收者地址")
        return
      }

      this.$emit('generateTransactionParams', {
        address: this.receiver,
        inscriptionId: this.nftId,
        options: {
          feeRate: "15"
        }
      }, "fractal_sendInscription")
    }
  }
}
</script>

