<script>


import {OKXTronProvider} from "@okxconnect/universal-provider";
import {TronWeb} from "tronweb";

export default {
  name: "TronTransactionPanel",
  methods: {

    async tron_signMessage() {
      this.$emit("generateTransactionParams", "Hello Tron")
      let provider = new OKXTronProvider(window.provider)
      let res = await provider.signMessage("Hello Tron", "tron:mainnet")
      if (res.code) {
        this.$emit("generateTransactionResult", res, "tron_signMessage")
        return
      }
      let tronWeb = new TronWeb({
        "fullHost": 'https://api.trongrid.io',
        "headers": {},
        "privateKey": ''
      })

      const verify = await tronWeb.trx.verifyMessage("0x" + Buffer.from("Hello Tron").toString("hex"), res, provider.getAccount("tron:mainnet").address)
      this.$emit("generateTransactionResult", "verify:" + verify + "\n" + res, "tron_signMessage")
    },

    async tron_signMessageV2() {
      this.$emit("generateTransactionParams", "Hello Tron")
      let provider = new OKXTronProvider(window.provider)
      let res = await provider.signMessageV2("Hello Tron", "tron:mainnet")
      if (res.code) {
        this.$emit("generateTransactionResult", res, "tron_signMessage")
        return
      }
      let tronWeb = new TronWeb({
        "fullHost": 'https://api.trongrid.io',
        "headers": {},
        "privateKey": ''
      })
      const base58Address = await tronWeb.trx.verifyMessageV2("Hello Tron", res);
      console.log("tron_signMessageV2_base58Address", base58Address)
      console.log("tron_signMessageV2_address", provider.getAccount("tron:mainnet").address)
      const verify = base58Address === provider.getAccount("tron:mainnet").address
      this.$emit("generateTransactionResult", "verify:" + verify + "\n" +  res, "tron_signMessage")
    },

    async tron_signTransaction(isOnlySign) {
      let provider = new OKXTronProvider(window.provider)
        let tronWeb = new TronWeb({
          "fullHost": 'https://api.trongrid.io',
          "headers": {},
          "privateKey": ''
        })
      let address = provider.getAccount("tron:mainnet").address
      const transaction = await tronWeb.transactionBuilder.sendTrx("TGBcVLMnVtvJzjPWZpPiYBgwwb7th1w3BF", 1000, address);
      this.$emit("generateTransactionParams", transaction)

      if (isOnlySign) {
        let res = await provider.signTransaction( transaction, "tron:mainnet")
        this.$emit("generateTransactionResult", res, "tron_signTransaction")
      } else {
        let tx = await provider.signAndSendTransaction(transaction,"tron:mainnet")
        this.$emit("generateTransactionResult", tx, "tron_sendRawTransaction")
      }
    },

    async getContract() {
      let tronWeb = new TronWeb({
        "fullHost": 'https://api.trongrid.io',
        "headers": {},
        "privateKey": ''
      })
      let contractAddress = "41e95812d8d5b5412d2b9f3a4d5a87ca15c5c51f33"
      
      try {
        const contract = await tronWeb.trx.getContract(contractAddress)
        const abi = contract.abi.entrys;
        console.log("获取到的合约：")
        console.log(abi)
      } catch (error) {
        console.log("Error getting contract ABI:", error);
      }
    },
    
    async tron_signAndSendTransactionContract(isOnlySign) {
      // this.getContract()
      let tronWeb = new TronWeb({
        "fullHost": 'https://api.trongrid.io',
        "headers": {},
        "privateKey": ''
      })
      let contractAddress = "41e95812d8d5b5412d2b9f3a4d5a87ca15c5c51f33"
      let provider = new OKXTronProvider(window.provider)
      let address = provider.getAccount("tron:mainnet").address
      const parameter = [
        {type: 'uint256', value: 1},
        {type: 'uint256', value: 1},
        {type: 'uint256', value: 1},
      ]
      const ret = await tronWeb.transactionBuilder.triggerSmartContract(contractAddress, "quote(uint256,uint256,uint256)", {}, parameter, address);
      console.log("=====>transaction: ")
      console.log(ret);
      this.$emit("generateTransactionParams", ret.transaction)
      if (isOnlySign) {
        let tx = await provider.signTransaction( ret.transaction,"tron:mainnet")
        this.$emit("generateTransactionResult", tx, "tron_sendRawTransaction")
      } else {
        let tx = await provider.signAndSendTransaction(ret.transaction,"tron:mainnet")
        this.$emit("generateTransactionResult", tx, "tron_sendRawTransaction")
      }
    },

    async tron_signAndSendTransactionApprove(isOnlySign) {
      let provider = new OKXTronProvider(window.provider)

      let transaction = {
        "visible": false,
        "txID": "d1ed31b6abd2c37e248254681d3ee61b2e628fbd776def753b5592712ebd98c5",
        "raw_data": {
          "contract": [{
            "parameter": {
              "value": {
                "data": "095ea7b3000000000000000000000000e95812d8d5b5412d2b9f3a4d5a87ca15c5c51f33ffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffffff",
                "owner_address": "4157c140be01fa2bbabf7f055ab879d0c05725293c",
                "contract_address": "41b4a428ab7092c2f1395f376ce297033b3bb446c1"
              },
              "type_url": "type.googleapis.com/protocol.TriggerSmartContract"
            },
            "type": "TriggerSmartContract"
          }],
          "ref_block_bytes": "1bde",
          "ref_block_hash": "af71fa1aa01b5bd1",
          "expiration": Date.now() + 60000,
          "fee_limit": 250000000,
          "timestamp": Date.now()
        },
        "raw_data_hex": ""
      }
      this.$emit("generateTransactionParams", transaction)

      if (isOnlySign) {
        let tx = await provider.signTransaction(transaction,"tron:mainnet")
        this.$emit("generateTransactionResult", tx, "tron_sendRawTransaction")
      } else {
        let tx = await provider.signAndSendTransaction(transaction,"tron:mainnet")
        this.$emit("generateTransactionResult", tx, "tron_sendRawTransaction")
      }

    }

  }
}
</script>

<template>
  <el-row :gutter="12" class="button-row">
    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="tron_signMessage()">tron_signMessage</el-button>
    </el-col>
    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="tron_signMessageV2()">tron_signMessageV2</el-button>
    </el-col>

    <div class="line"></div>


    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="tron_signTransaction(true)">
        tron_signTransaction(transfer)
      </el-button>
    </el-col>

    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="tron_signAndSendTransactionContract(true)">
        tron_signTransaction(contract)
      </el-button>
    </el-col>

    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="tron_signAndSendTransactionApprove(true)">
        tron_signTransaction(Approve)
      </el-button>
    </el-col>

    <div class="line"></div>


    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="tron_signTransaction(false)">
        tron_signAndSendTransaction(transfer)
      </el-button>
    </el-col>

    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="tron_signAndSendTransactionContract(false)">
        tron_signAndSendTransaction(contract)
      </el-button>
    </el-col>

    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="tron_signAndSendTransactionApprove(false)">
        tron_signAndSendTransaction(approve)
      </el-button>
    </el-col>


  </el-row>
</template>

<style scoped>
.grid-content {
  margin-top: 8px;
  height: 40px;
  width: 100%;
}

.line {
  height: 2px;
  width: 100%;
  margin: 20px 0px;
  background-color: gray;
}
</style>