<script>

import {getFullnodeUrl, SuiClient} from "@mysten/sui/client";
import {Transaction as SuiTransaction} from '@mysten/sui/transactions';

export default {
  name: "SuiTransactionPanel",
  methods: {
    sui_getAccount() {
      this.$emit('generateTransactionParams', '', "sui_getAccount")
    },

    //对单个交易签名（不发送)
    async sui_signMessage() {
      const data = [
        76, 111, 103, 105, 110, 32, 119, 105, 116, 104, 32, 66, 108, 117, 101,
        109, 111, 118, 101,
      ];
      const uint8Array = new Uint8Array(data);
      let input = {
        message: uint8Array,
      };
      this.$emit("generateTransactionParams", input, "sui_signMessage")
    },

    async sui_signPersonalMessage() {
      const data = [
        76, 111, 103, 105, 110, 32, 119, 105, 116, 104, 32, 66, 108, 117, 101,
        109, 111, 118, 101,
      ];
      const uint8Array = new Uint8Array(data);
      let input = {
        message: uint8Array,
      };
      this.$emit("generateTransactionParams", input, "sui_signPersonalMessage")
    },

    sui_signTransaction2() {
      // 定义要转移的金额和目标地址
      const amount = 109; // 需要转移的金额
      /// Rally Tiger钱包地址
      const recipientAddress =
          "0x72f798f8b709902453d4bb55c65661e9e04154a506765f2333dfb7e7834056d2"; // 目标地址

      /// 构造一个转账的交易
      const tx = new SuiTransaction();
      const [coin] = tx.splitCoins(tx.gas, [amount]);
      tx.transferObjects([coin], recipientAddress);
      const input = {
        transactionBlock: tx,
        account: {},
        chain: "sui:mainnet",
        options: {
          showEffects: true,
        },
      };

      this.$emit("generateTransactionParams", input, "sui_signTransaction")
    },

    async sui_requestRpc() {
      let provider = window.provider;
      provider.setDefaultChain(
          `sui:mainnet`,
          `https://fullnode.mainnet.sui.io:443`
      );
      var data = {
        method: "suix_getBalance",
        params: [
          "0x51ceab2edc89f74730e683ebee65578cb3bc9237ba6fca019438a9737cf156ae",
          "0x168da5bf1f48dafc111b0a488fa454aca95e0b5e::usdc::USDC",
        ],
      };

      this.$emit("generateTransactionParams", data, "sui_testRpc")
    },

    /// 预执行，提前生成txBytes和txSerizalize
    async sui_signTransaction_pre_execution() {
      // 定义要转移的金额和目标地址
      const amount = 102; // 需要转移的金额
      /// Rally Tiger钱包地址
      const recipientAddress =
          "0x72f798f8b709902453d4bb55c65661e9e04154a506765f2333dfb7e7834056d2"; // 目标地址

      /// 构造一个转账的交易
      const tx = new SuiTransaction();
      const [coin] = tx.splitCoins(tx.gas, [amount]);
      tx.transferObjects([coin], recipientAddress);
      const input = {
        transactionBlock: tx,
        options: {
          showEffects: true,
        },
      };

      /// 预执行，生成txBytes和txSerialize
      const [txBytes, txSerialize] = await this.generateTsBytesAndSerizlize(
          input
      );
      input.txBytes = txBytes;
      input.txSerialize = txSerialize;

      this.$emit("generateTransactionParams", input, "sui_signTransaction")
    },

    /// 预执行，提前生成txBytes和txSerizalize
    async sui_signAndExecuteTransaction_pre_execution() {
      // 定义要转移的金额和目标地址
      const amount = 115; // 需要转移的金额
      /// Rally Tiger钱包地址
      const recipientAddress =
          "0x72f798f8b709902453d4bb55c65661e9e04154a506765f2333dfb7e7834056d2"; // 目标地址

      /// 构造一个转账的交易
      const tx = new SuiTransaction();
      const [coin] = tx.splitCoins(tx.gas, [amount]);
      tx.transferObjects([coin], recipientAddress);

      const input = {
        transactionBlock: tx,
        options: {
          showEffects: true,
        },
      };

      /// 预执行，生成txBytes和txSerialize
      const [txBytes, txSerialize] = await this.generateTsBytesAndSerizlize(
          input
      );
      input.txBytes = txBytes;
      input.txSerialize = txSerialize;
      this.$emit("generateTransactionParams", input, "sui_signAndExecuteTransaction")
    },

    /// generate txBytes and txSerialize
    async generateTsBytesAndSerizlize(input) {
      /// txBytes and txSerialize may be constructed in advance by the dapp side
      let resultTuple = ["", ""];
      if (
          "txBytes" in input &&
          input.txBytes.length > 0 &&
          "txSerialize" in input &&
          input.txSerialize.length > 0
      ) {
        resultTuple = [input.txBytes, input.txSerialize];
      }

      /// Otherwise, a transactionBlock parameter is required.
      if (!("transactionBlock" in input)) {
        return new Promise((resolve) => {
          resolve(resultTuple);
        });
      }

      // if (this.walletAddress != "connected") {
      //   alert("请先连接钱包");
      //   return new Promise((resolve) => {
      //     resolve(resultTuple);
      //   });
      // }

      let senderAccounts = window.provider.requestAccountsWithNamespace("sui");
      if (senderAccounts.length == 0) {
        return new Promise((resolve) => {
          resolve(resultTuple);
        });
      }

      try {
        let rpcUrl = getFullnodeUrl("mainnet");
        rpcUrl = "https://www.okx.com/fullnode/sui/discover/rpc"
        const suiClient = new SuiClient({ url: rpcUrl });

        /// sender is required
        input.transactionBlock.setSender(senderAccounts[0]);
        const result = await input.transactionBlock.build({
          client: suiClient,
        });
        const txBytes = Buffer(result).toString("base64");
        const txSerialize = await SuiTransaction.from(txBytes).toJSON();
        resultTuple = [txBytes, txSerialize];
      } catch (error) {
        return new Promise((resolve) => {
          resolve(resultTuple);
        });
      }

      return new Promise((resolve) => {
        resolve(resultTuple)
      })
    },

    //签名并发送交易
    sui_signAndExecuteTransaction() {
      // 定义要转移的金额和目标地址
      const amount = 115; // 需要转移的金额
      /// Rally Tiger钱包地址
      const recipientAddress =
        "0x72f798f8b709902453d4bb55c65661e9e04154a506765f2333dfb7e7834056d2"; // 目标地址

      /// 构造一个转账的交易
      const tx = new SuiTransaction();
      const [coin] = tx.splitCoins(tx.gas, [amount]);
      tx.transferObjects([coin], recipientAddress);
      const input = {
        transactionBlock: tx,
        account: {},
        chain: "sui:mainnet",
        options: {
          showEffects: true,
        },
      };

      this.testTransaction = JSON.stringify(input);
      this.currentSuiTransaction = input;
      this.currentSuiMethod = "sui_signAndExecuteTransaction";
    },
  },
}
</script>

<template>
  <el-row :gutter="12" class="button-row">
    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="sui_getAccount()">getAccount(Sui)</el-button>
    </el-col>
    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="sui_signMessage()">signMessage(Sui)</el-button>
    </el-col>
    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="sui_signPersonalMessage()">signPersonalMessage(Sui)</el-button>
    </el-col>
    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="sui_signTransaction2()">signTransaction(Sui)</el-button>
    </el-col>

    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="sui_signAndExecuteTransaction()">sendTransaction(Sui)</el-button>
      <div style="font-size: 12px; color: gray;">(send to Rally Tiger account)</div>
    </el-col>

    <el-col :span="24">
      <el-button class="grid-content" type="primary" @click="sui_requestRpc()">RPC_getBalance</el-button>
      <div style="font-size: 12px; color: gray;">get USDC balance of address:</div>
      <div style="font-size: 12px; color: gray">0x51ceab2edc89f74730e683ebee65578cb3bc9237ba6fca019438a9737cf156ae</div>
    </el-col>

    <div class="line"></div>
    <el-col :span="24">
      <el-button class="grid-content" type="primary"
                 @click="sui_signTransaction_pre_execution()">signTransaction(prepared data)</el-button>
    </el-col>

    <el-col :span="24">
      <el-button class="grid-content" type="primary"
                 @click="sui_signAndExecuteTransaction_pre_execution()">sendTransaction(prepared data)</el-button>
    </el-col>
  </el-row>
</template>

<style scoped>
.grid-content {
  margin-top: 8px;
  height: 40px;
  width: 100%;
}

.line {
  height: 2px;
  width: 100%;
  margin: 20px 0px;
  background-color: gray;
}
</style>