        binary += String.fromCharCode(byte);
      });

      let firstStr =  btoa(binary)
      console.log("字符串编码1>>>>",firstStr)
      firstStr = firstStr.replace(/\+/g, '-')  // 替换 '+'
      console.log("字符串编码2>>>>",firstStr)
      firstStr = firstStr.replace(/\//g, '_')  // 替换 '/'
      console.log("字符串编码3>>>>",firstStr)
      firstStr = firstStr.replace(/=+$/, '');  // 移除 '=';
      console.log("字符串编码4>>>>",firstStr)

      let showUrl =  getUniversalLink(getAppWalletDeepLink(firstStr))
      console.log("字符串编码5>>>>",showUrl)
      this.qrValue = showUrl;
      this.showModal = true;
    },


    async testTonSdk() {
      // 1. connect
      //  create sub providers for each namespace/chain
      let nameSpacesChains = [];
      let optSpaceChains = [];

      let solanaChains = [
        "solana:5eykt4UsFv8P8NJdTREpY1vzqKqZKvdp",
        "solana:4uhcVJyU9pJkvQyS88uRDiswHXSCkY3z",
        "svm:70000062",
      ];
      let hasEip155, hasSolana, hasSui, hasBtc, hasAptos, hasTon, hasCosmos, hasTron = false

      let suiChains = [
        "sui:mainnet",
      ];

      let btcChains = [
        "btc:mainnet",
        "fractal:mainnet"
      ];

      const chains = Object.entries(this.availableChains)
          .filter((chain) => chain[1].checked)
          .map((chain) => chain[0]);
      console.log(`chains  ===> ${chains.values()}`)
      chains.forEach(function (chainId) {
        console.log(`chains.forEach chainId  ===> ${chainId}`)

        if (solanaChains.includes(chainId)) {
          nameSpacesChains.push(chainId);
          hasSolana = true;
          return;
        }
        if (typeof chainId == 'string' && chainId.startsWith("ton")) {
          nameSpacesChains.push(chainId)
          hasTon = true
          return
        }
        if (typeof chainId == 'string' && (chainId.startsWith("aptos") || chainId.startsWith("movement"))) {
          nameSpacesChains.push(chainId)
          hasAptos = true
          return
        }
        if (typeof chainId == 'string' && chainId.startsWith("svm")) {
          nameSpacesChains.push(chainId)
          hasSolana = true
          return
        }
        if (typeof chainId == 'string' && chainId.startsWith("cosmos")) {
          nameSpacesChains.push(chainId)
          hasCosmos = true
          return
        }
        if (typeof chainId == 'string' && chainId.startsWith("tron")) {
          nameSpacesChains.push(chainId)
          hasTron = true
          return
        }

        if (suiChains.includes(chainId)) {
          console.log(`nameSpacesChains.push  ===> ${chainId}`);
          nameSpacesChains.push(chainId);
          hasSui = true;
          return;
        }

        if (btcChains.includes(chainId)) {
          console.log(`nameSpacesChains.push  ===> ${chainId}`)
          nameSpacesChains.push(chainId)
          hasBtc = true
          return
        }

        hasEip155 = true
        nameSpacesChains.push(`eip155:${chainId}`)
      })


      optionChains.forEach(function (chainId) {
        if (solanaChains.includes(chainId)) {
          optSpaceChains.push(chainId);
          return;
        }
        if (typeof chainId == "string" && chainId.startsWith("svm")) {
          nameSpacesChains.push(chainId);
          hasSolana = true;
          return;
        }
        if (suiChains.includes(chainId)) {
          optSpaceChains.push(chainId);
          return;
        }

        if (btcChains.includes(chainId)) {
          optSpaceChains.push(chainId)
          return
        }

        optSpaceChains.push(`eip155:${chainId}`)
      })

      console.log(`namespaceChains  ===> ${JSON.stringify(nameSpacesChains)}`);
      var connectData = {};
      if (nameSpacesChains && nameSpacesChains.length > 0) {
        var requireData = {};

        if (hasEip155) {
          requireData.eip155 = {
            chains: nameSpacesChains.filter((item) =>
              item.startsWith("eip155")
            ),
            defaultChain: nameSpacesChains.filter((item) =>
              item.startsWith("eip155")
            )[0],
          };
        }
        if (hasSolana) {
          requireData.solana = {
            chains: nameSpacesChains.filter(
              (item) =>
                item.startsWith("solana") ||
                item.startsWith("sonic") ||
                item.startsWith("svm")
            ),
            defaultChain: nameSpacesChains.filter(
              (item) =>
                item.startsWith("solana") ||
                item.startsWith("sonic") ||
                item.startsWith("svm")
            )[0],
          };
        }

        if (hasCosmos) {
          requireData.cosmos = {
            chains: nameSpacesChains.filter(
                (item) =>
                    item.startsWith("cosmos")
            ),
            defaultChain: nameSpacesChains.filter(
                (item) =>
                    item.startsWith("cosmos")
            )[0],
          }
        }
        if (hasTron) {
          requireData.tron = {
            chains: nameSpacesChains.filter(
                (item) =>
                    item.startsWith("tron")
            ),
            defaultChain: nameSpacesChains.filter(
                (item) =>
                    item.startsWith("tron")
            )[0],
          }
        }
        if (hasTon) {

          //
          let tonParams = {
            ton_addr:{
              name: 'ton_addr',
            }
          };
          if (this.tonProofValue){
            tonParams = {
              ton_addr:{
                name: 'ton_addr',
              },
              ton_proof:{
                name: 'ton_proof',
                payload: this.tonProofValue,
              }
            };
          }


          requireData.ton = {
            chains: nameSpacesChains.filter(item => item.startsWith("ton")),
            defaultChain: nameSpacesChains.filter(item => item.startsWith("ton"))[0],
            params:tonParams
          };
        }

        if (hasSui) {
          requireData.sui = {
            chains: nameSpacesChains.filter((item) => item.startsWith("sui")),
            defaultChain: nameSpacesChains.filter((item) =>
              item.startsWith("sui")
            )[0],
            rpcMap: {
              "sui:mainnet": "https://www.okx.com/fullnode/sui/discover/rpc"
            }
          };
        }
        if (hasAptos) {
          requireData.aptos = {
            chains: nameSpacesChains.filter(item => item.startsWith("aptos") || item.startsWith("movement")),
            defaultChain: nameSpacesChains.filter(item => item.startsWith("aptos") || item.startsWith("movement"))[0]
          }
        }

        if (hasBtc) {
          let btcChains = nameSpacesChains.filter(item => item.startsWith("btc") || item.startsWith("fractal"))
          requireData.btc = {
            chains: btcChains,
            defaultChain: btcChains[0]
          }
        }
        console.log(`requireData  ===> ${JSON.stringify(requireData)}`)
        connectData['namespaces'] = requireData;
        console.log(JSON.stringify(connectData))
      }

      if (optSpaceChains && optSpaceChains.length > 0) {
        var optData = {
          eip155: {
            chains: optSpaceChains.filter((item) => item.startsWith("eip155")),
            defaultChain: optSpaceChains.filter((item) =>
              item.startsWith("eip155")
            )[0],
          },
        };
        connectData["optionalNamespaces"] = optData;
      }

      if (nameSpacesChains.length === 0 && optSpaceChains.length === 0) {
        //未选择链
        alert("Please select a chain from above to continue");
        return;
      }
      console.log("=====>connectData ");
      console.log(connectData);

      var connectMethod;
      let signMethods = this.getCurrentSelectedMethodData();
      if (window.provider instanceof OKXUniversalConnectUI) {
        if (signMethods && signMethods.length>0){
          connectMethod = window.provider.openModalAndSign(connectData,signMethods);
        }else {
          connectMethod = window.provider.openModal(connectData);
        }
      } else {
        if (this.universalToTG){
          connectData.sessionConfig = {
            useMini:true,
            openUniversalUrl:true,
            tmaReturnUrl:'back'
          }
        }else{
          connectData.sessionConfig = {
            redirect: selectedReturnStrategy
          }
        }
        if (signMethods && signMethods.length>0){
          connectMethod = window.provider.connectAndSign(connectData,signMethods);
        }else {
          connectMethod = window.provider.connect(connectData);
        }
      }

      await connectMethod
        .then((result) => {
          // 2. set defaul chain
          this.walletAddress = "connected";
          if (this.isPC()) {
            this.showModal = false;
          }
          if (result){
            this.testboc = JSON.stringify(result)
          }

          if (window.provider.session && window.provider.session.namespaces){
            let ton = window.provider.session.namespaces["ton"];
          console.log("是否包含ton",ton)
            if (ton){
              this.tonUniversalLog()
            }
          }



        })
        .catch((error) => {
          if (error.message){
            alert(error.message)
          }

          console.error("发生了错误：", error);
        });
    },

    getCurrentSelectedMethodData(){// 连接签名
      var methodList = [];
      this.selectedMethod.forEach(method =>{
        switch (method) {
          case "wallet_addEthereumChain(ETH)":
            methodList.push({
              method: "wallet_addEthereumChain",
              chainId:"eip155:1",
              params: {
                blockExplorerUrls: ["https://explorer.fuse.io"],
                chainId: "0x7a",
                chainName: "Fuse",
                nativeCurrency: { name: "Fuse", symbol: "FUSE", decimals: 18 },
                rpcUrls: ["https://rpc.fuse.io"],
              },
            })
            break;
          case "personal_sign(ETH)":
            methodList.push(
              {
                method: "personal_sign",
                chainId: "eip155:1",
                params: [
                  "0x4d7920656d61696c206973206a6f686e40646f652e636f6d202d2031373237353937343537313336",
                ],
              });
            break;
          case "solana_signMessage(Sol mainnet)":
