                  "method": "cosmos_signArbitrary",
                  "params": {
                    message: "test cosmos"
                  }
                }
            )

            break;
          case "test_noSupport_eth(ETH)":
            methodList.push(
                {
                  chainId: "eip155:1",
                  method: "test_noSupport_eth",
                  params: {
                    message: "test hello",
                  }
                }
            )
            break;
          case "sync_all_addresses":
            methodList.push(
                {
                  method: "sync_all_addresses",
                });
            break;
          case "tron_signMessage":
            methodList.push(
                {
                  chainId:"tron:mainnet",
                  method: "tron_signMessage",
                  params: {
                    message: "Hello Tron",
                  }
                }
            )
            break
          case "tron_signMessageV2":
