                {
                  chainId:"tron:mainnet",
                  method: "tron_signMessageV2",
                  params: {
                    message: "Hello Tron",
                  }
                }
            )
            break
            //
          default:

            break;
        }
      })
      this.selectedMethodData = methodList
      return methodList

