    eth_verifyMessage(message, signature,ethAddress) {
      console.log("eth_verifyMessage   message>>>",message)
      console.log("eth_verifyMessage   signature>>>",signature)
      console.log("eth_verifyMessage   ethAddress>>>",ethAddress)
      if (!message || !signature){
        return ""
      }
      let originalMessage = message
      if (message.startsWith("0x")){
        originalMessage = originalMessage.replace(/^0x/, '');
        let str = '';
        for (let i = 0; i < originalMessage.length; i += 2) {
          const byte = originalMessage.slice(i, i + 2);
          str += String.fromCharCode(parseInt(byte, 16));
        }
        originalMessage = str
      }
      console.log("eth_verifyMessage   originalMessage>>>",originalMessage)

      // const messageHash = ethers.utils.hashMessage(originalMessage);
      const recoveredAddress = ethers.utils.verifyMessage(originalMessage, signature);
      console.log("eth_verifyMessage recoveredAddress:", recoveredAddress);
      return recoveredAddress.toLowerCase() === ethAddress.toLowerCase();
    },

    async verifyMessage(list){ //连接验签

        if (window.provider && window.provider.session && list && list.length>0){
          let verifyList = {}
          for (let methodItem of list){
            if ("result" in methodItem && "method" in methodItem){
              let requestItme = this.selectedMethodData.filter(item => item.method === methodItem.method)[0];
              console.log("验证签名 请求>>>:", JSON.stringify(requestItme));
              console.log("验证签名 结果>>>:", JSON.stringify(methodItem));
              if (methodItem.method === "personal_sign"){
                let ethAddress = await window.provider.request({ method: "eth_requestAccounts" },methodItem.chainId)
                ethAddress = ethAddress[0];
                console.log("验证eth 钱包地址>>>:", ethAddress);
                let ethMessage = requestItme.params[0]
                console.log("验证eth 签名消息体>>:", JSON.stringify(ethMessage));
                verifyList[methodItem.method] = this.eth_verifyMessage(ethMessage,methodItem.result,ethAddress)
              }else if(methodItem.method === "solana_signMessage"){
                let solanaMessage = methodItem.result
                if ("signature" in solanaMessage && "publicKey" in solanaMessage){
                  const verifySol = nacl.sign.detached.verify(
                      new TextEncoder().encode("Hello Solana"),
                      solanaMessage.signature,
                      new PublicKey(solanaMessage.publicKey).toBytes()
                  );
                  verifyList[methodItem.method] = verifySol
                }else {
                  verifyList[methodItem.method] = false
                }
              }else if(methodItem.method === "sui_signPersonalMessage" || methodItem.method === "sui_signMessage"){
                let suiResult =  this.sui_verifySignature(methodItem.result);
                  verifyList[methodItem.method] = (await suiResult).result
              }else if(methodItem.method === "btc_signMessage"){

                verifyList[methodItem.method] = "Not verified yet"

              }else if(methodItem.method === "aptos_signMessage"){
                let aptosMessage = methodItem.result
                let aptosProvider = new OKXAptosProvider(window.provider)
                let  aptosPublicKey  = aptosProvider.getAccount(methodItem.chainId)?.publicKey

                let signStr = aptosMessage.signature
                if (signStr.startsWith("0x")) {
                  signStr = signStr.slice(2, signStr.length)
                }

                console.log("aptos_signMessage aptosPublicKey And signStr",aptosPublicKey,signStr)
                verifyList[methodItem.method] = nacl.sign.detached.verify(
                    Buffer.from(aptosMessage.fullMessage),
                    Buffer.from(signStr, 'hex'),
                    Buffer.from(aptosPublicKey, 'hex'),
                );
              }else if(methodItem.method === "cosmos_signArbitrary"){
                let cosmosProvider = new OKXCosmosProvider(window.provider)
                let cosmosAddress = cosmosProvider.getAccount(methodItem.chainId)?.address
                let cosmosMessage = methodItem.result
                let isValid =  await this.cosmos_verifySignArbitrary(cosmosAddress,"test cosmos",cosmosMessage.signature, cosmosMessage.pub_key.value)
                verifyList[methodItem.method] = isValid

              }else if (methodItem.method === "tron_signMessage") {
                let tronMessage = methodItem.result
                let tronWeb = new TronWeb({
                  "fullHost": 'https://api.trongrid.io',
                  "headers": {},
                  "privateKey": ''
                })
                let provider = new OKXTronProvider(window.provider)
                verifyList[methodItem.method] = await tronWeb.trx.verifyMessage("0x" + Buffer.from("Hello Tron").toString("hex"), tronMessage, provider.getAccount("tron:mainnet").address)
              } else if (methodItem.method === "tron_signMessageV2") {
                let tronMessage = methodItem.result
                let tronWeb = new TronWeb({
                  "fullHost": 'https://api.trongrid.io',
                  "headers": {},
                  "privateKey": ''
                })
                const base58Address = await tronWeb.trx.verifyMessageV2("Hello Tron", tronMessage);
                let provider = new OKXTronProvider(window.provider)

                verifyList[methodItem.method] = base58Address === provider.getAccount("tron:mainnet").address
              }
              else {
                verifyList[methodItem.method] = "Not verified yet"

              }
            }else if("method" in methodItem && "error" in methodItem) {
              verifyList[methodItem.method] = methodItem.error
            }

          }

          console.log("验证签名 最后的结果>>>:", JSON.stringify(verifyList));
          this.testTransaction = "签名验证结果如下: \n" + JSON.stringify(verifyList)
        }else {
          console.log("验证签名 window.provider.session.connectMethodsReply 为空:");

        }

    },

