    async sui_verifySignature(signatureResult) {
      let resultObj = signatureResult
      let result = false
      let msg = ''
      try {
        /// 获取当前account
        const adapter = new SuiAdapter(window.provider);
        let account = adapter.getAccount()
        console.log(`Account中获取的address：${account.address}`)

        /// 验证签名
        let pubKey = await verifyPersonalMessageSignature(this.currentSuiTransaction.message, resultObj.signature)
        let address = pubKey.toSuiAddress()
        console.log(`验签结果计算得到address：${address}`)

        /// 对比pubKey
        console.log(`account pubkey: ${account.publicKey}`,)
        console.log(`result pubkey: ${pubKey.toBase64()}`)
        result = (account.address === address) && (account.publicKey === pubKey.toBase64())

        msg = `address verify: ${account.address === address}\npublicKey verify: ${account.publicKey === pubKey.toBase64()}`
        console.log("signature verify success: ", pubKey)
      } catch (e) {
        console.log("signature verify failed: ", e)
      }
      return {
        result,
        msg
      }
    },


    handleRedirectSelect(input) {
      this.redirect = input;
    },

    handleRedirectDeeplink(input) {
      this.redirectDeeplink = input;
    },

    addRedirectParam() {
      if (this.testTransaction) {
        const newTransaction = JSON.parse(this.testTransaction);
        newTransaction.redirect = this.redirect === 'deeplink' ? this.redirectDeeplink : this.redirect;
        this.testTransaction = JSON.stringify(newTransaction);
      }
    },

    canChainAddRedirectParam() {
      return chainsThatCanAddRedirectParam.some((c) => this.selectedChain.startsWith(c));
    }
  },
  created() {
    // for (const eventName of Object.values(OKX_TON_CONNECTION_AND_TRANSACTION_EVENT)) {
